/**
 * 配置编译环境和线上环境之间的切换
 *
 * baseUrl: 域名地址
 * routerMode: 路由模式
 * baseImgPath: 图片存放地址
 *
 */
let baseUrl = ''
let routerMode = 'hash'
let baseImgPath

if (process.env.NODE_ENV === 'development') {
  baseUrl = '//192.168.100.5:8001'
  baseImgPath = '//192.168.100.5:8001/img/'
} else {
  baseUrl = '//192.168.100.5:8001'
  baseImgPath = '//192.168.100.5:8001/img/'
}

export {
  baseUrl,
  routerMode,
  baseImgPath
}



